/* counter up */
.counter-up{
  background:url(/metalanalysis/static/media/counter-image.aef8d954.jpg) no-repeat;
  min-height: 50vh;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  padding: 0 50px;
  background-size: cover;
  background-attachment: fixed;
  position: relative;
  /* background-position: center; */
 
 
}
.counter-up::before{
  position: absolute;
  content: "";
  top: 0;
  left: 0;
  height: 100%;
  width:100%;
  background: rgba(0, 0, 0, 0.8);
}
.counter-up .content{
  position: relative;
  z-index: 2;
  display: -webkit-flex;
  display: flex; 
  width: 100%;
  height: 100%;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: space-between;
          justify-content: space-between;
}
.content .box{
  width: calc(25% - 30px);
  border: 1px dashed #10b7eb99;
  border-radius: 5px;
  padding: 20px;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content:space-evenly;
          justify-content:space-evenly;
  color: #fff;
  -webkit-flex-direction: column;
          flex-direction: column;
  border-radius: 5px;
}
.content .box .icon{
  font-size: 48px;
  color: #10b7eb99;
}
.content .box .counter{
  font-size: 50px;
  color: #f2f2f2;
  font-weight: 500;
}
.content .box .text{
  color: #fff;
  font-weight: 400;
  text-transform: uppercase;
}
@media (max-width: 1036px){
  .counter-up{
    padding: 50px 50px 0 50px;
  }
  .content .box{
    width: calc(50% - 30px);
    margin-bottom:50px;
  }
}
@media (max-width: 580px){
  .content .box{
    width:100%;
  }
}
/* SliderLOgo */
.bodyS{
  min-height: 40vh;
  display: grid;
  place-items: center;
}
.sliderlogo{
  height: 250px;
  margin: auto;
  position:relative;
  width: 90%;
  display:grid;
  place-items: center;
  overflow: hidden;
}
.slide-track{
  display: -webkit-flex;
  display: flex;
  width: calc(250px * 12);
  -webkit-animation: scroll 40s linear infinite;
          animation: scroll 40s linear infinite;
}

@-webkit-keyframes scroll {
  0%{
    -webkit-transform: translateX(0);
            transform: translateX(0);

  }
  100%{
    -webkit-transform: translateX(calc(-250px * 6));
            transform: translateX(calc(-250px * 6));
  }
}

@keyframes scroll {
  0%{
    -webkit-transform: translateX(0);
            transform: translateX(0);

  }
  100%{
    -webkit-transform: translateX(calc(-250px * 6));
            transform: translateX(calc(-250px * 6));
  }
}

.slideS{
  height: 100px;
  width: 200px;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  padding: 15px;
  -webkit-perspective: 100px;
          perspective: 100px;
}
#imgS{
  width: 100%;
  transition:-webkit-transform 1s;
  transition:transform 1s;
  transition:transform 1s, -webkit-transform 1s;
}
#imgS:hover{
 -webkit-transform: translateZ(20px);
         transform: translateZ(20px);
}
/* Gradient Shadow */

.sliderlogo::before,
.sliderlogo::after{
  background: linear-gradient(to right, rgba(255,255,255,1) 0%, rgba(255, 255, 255, 0) 100%);
  content: "";
  height: 100%;
  position:absolute;
  width: 15%;
  z-index: 2;
}
.sliderlogo::before{
  left:0;
  top:0
}
.sliderlogo::after{
  right: 0;
  top: 0;
  -webkit-transform: rotateZ(180deg);
          transform: rotateZ(180deg);
}



